import React, { useState, useEffect } from 'react'
import { Link } from 'react-router-dom'
import CIcon from '@coreui/icons-react'
import { cilDataTransferDown } from '@coreui/icons'
import { CContainer, CRow, CCol, CButton } from '@coreui/react'
import { endLists } from '../../mocks/quotationData'
import Pagenate from '../Share/Pagenate'

const QuotationEnd = () => {
  const [data, setData] = useState([])
  const [currentPage, setCurrentPage] = useState(0)
  const [totalPages, setTotalPages] = useState(0)
  const itemsPerPage = 10

  useEffect(() => {
    // axios.get('https://jsonplaceholder.typicode.com/posts/').then((response) => {
    setData(endLists)
    setTotalPages(Math.ceil(endLists.length / itemsPerPage))
    // })
  }, [])

  const startIndex = currentPage * itemsPerPage
  const endIndex = startIndex + itemsPerPage
  const subset = data.slice(startIndex, endIndex)

  const handlePageChange = (selectedPage) => {
    setCurrentPage(selectedPage.selected)
  }
  return (
    <div className="lists-container">
      <CContainer>
        <div className="list-head">
          <CRow>
            <CCol className="text-align-center" sm={2}>
              報價單編號
            </CCol>
            <CCol className="text-align-center" sm={3}>
              客戶統編
            </CCol>
            <CCol className="text-align-center" sm={2}>
              建立時間
            </CCol>
            <CCol className="text-align-center" sm={2}>
              狀態
            </CCol>
            <CCol className="text-align-center" sm={2}>
              操作
            </CCol>
            <CCol className="text-align-center" sm={1}>
              預覽/下載
            </CCol>
          </CRow>
        </div>
        <div className="list-body">
          {subset &&
            subset.length &&
            subset.map((item, i) => {
              return (
                <div className="list-item" key={item.num + i}>
                  <CRow className="align-items-center">
                    <CCol className="text-align-center" sm={2}>
                      {item.num}
                    </CCol>
                    <CCol className="text-align-center" sm={3}>
                      {item.client}
                    </CCol>
                    <CCol className="text-align-center" sm={2}>
                      {item.createDate}
                    </CCol>
                    <CCol className="text-align-center" sm={2}>
                      {item.statusText}
                    </CCol>
                    <CCol className="text-align-center" sm={2}>
                      <CButton color="warning">終止</CButton>
                    </CCol>
                    <CCol className="text-align-center" sm={1}>
                      <Link to="/pdfContainer" className="action-icon">
                        <CIcon icon={cilDataTransferDown} />
                      </Link>
                    </CCol>
                  </CRow>
                </div>
              )
            })}
        </div>

        {data.length > itemsPerPage && (
          <Pagenate
            totalPages={totalPages}
            currentPage={currentPage}
            handlePageChange={handlePageChange}
          />
        )}
      </CContainer>
    </div>
  )
}

export default QuotationEnd
